import React, { useState } from 'react';
import { HashRouter as Router, Switch, Route} from 'react-router-dom';
import './App.css';
import Header from './components/Header';
import Homepage from './components/Homepage';
import Addroom from './components/Addroom';
import RoomPropeties from './components/Room-Propeties';
import Menu from './components/Manu'



function App() {
  const [list, setList] = useState([]);
  const [listNotConcatenated, setListNotConcatenated] = useState([]);

 
  const addRoom = (name, color, type) => {
    if (name.length > 5 || name.length === 0) {
      alert('Error')
    } else {
      setList([...list, { name , color , type }])
      setListNotConcatenated([{ name , color, type}])
    }
  }



  return (
    <div className="App">

      <Header />
      <Router>
        <Menu/>
        <Switch>
          <Route exact path='/' component={() => <Homepage listRoom={list}/>}/>
           <Route exact path='/addroom' component={() => <Addroom add={addRoom} />} />
           <Route exact path='/room/:name' component={()=><RoomPropeties listRoom={listNotConcatenated}/>}/>
      </Switch>
    </Router>
    </div>
      );
    }
    
    export default App;
