import React, {useState} from 'react';
import { Link } from 'react-router-dom';

const BEDROOM_TYPE = 'Bedroom'
const BATHROOM_TYPE = 'Bathroom'
const KITCHEN_TYPE = 'Kitchen'

export default function Addroom(props) {

    const [roomType, setRoomType] = useState(BEDROOM_TYPE)
    const [color, setColor] = useState('');
    const [name, setName] = useState('');
    

    return (
      
        <div className='addroom'>
            <select value={roomType} onChange={(e) => { setRoomType(e.target.value) }} className='addroombutton'>
                <option value={BEDROOM_TYPE}>{BEDROOM_TYPE}</option>
                <option value={BATHROOM_TYPE}>{BATHROOM_TYPE}</option>
                <option value={KITCHEN_TYPE}>{KITCHEN_TYPE}</option>
            </select>
            <br/>
            <br/>
            <input type='text' placeholder='Room name' onChange={(e)=>{setName(e.target.value)}} className='addroombutton'></input>
            <br/>
            <br/>
            <input type='color' onChange={(e)=>{setColor(e.target.value)}} className='addroombutton' ></input>
            <br/>
            <br/>
            <Link to='/'><button onClick={()=>props.add(name, color, roomType)} className='addroombutton'>Create Room</button></Link>
            <br/>
        </div>
    )
}
