import React from 'react'
import { Link } from 'react-router-dom';


export default function Homepage(props) {


  const roomList = props.listRoom.map((e, i) => 
  <Link to={'/room/'+e.name} style={{ backgroundColor: e.color }} className='addroombutton'>{e.name}<br/></Link>)
  
  return (
    <div>
     <div>{roomList}</div>
     <br/>
        <Link to='/addroom'><button className='addroombutton' onClick={props.indexX} >+</button></Link>
    </div >
  )
}
