import React, { useState, useEffect } from 'react';

const AC = 'AC';
const LIGHT = 'Light';
const STERO_SYSTEM = 'Setro System';
const BOILER = 'Boiler';
const RED = 'red';
const GREEN = 'green';

export default function RoomPropeties(props) {
    const [flag, setFlag] = useState(false);
    const [product, setProduct] = useState(AC);
    const [productList, setProductList] = useState([]);
    // useEffect(()=> {
    //     const storedProduct = localStorage.getItem('productA');
    //     const storedProductList = localStorage.getItem('productList')
    //     if (storedProduct){
    //         setProduct(JSON.parse(storedProduct));
    //     }
    //     if (storedProductList){
    //         setProductList(JSON.parse(storedProductList));
    //     }

    // }, [])

    // useEffect(()=>{
    //     localStorage.setItem('productA', JSON.stringify(product))
    // }, [product])

    // useEffect(()=>{
    //     localStorage.setItem('productList', JSON.stringify(productList))
    // }, [productList])

    const roomName = props.listRoom.map((e) => e.name);
    const roomType = props.listRoom.map((e) => e.type);
   
    

    const addProduct = (name, color) => {
        if(productList.length<5){
            setProductList([...productList, { name, color }]);
        } else {
            alert('Error')
        }
    }
    const showProducts = productList.map((e, i) => <div key={i}><button style={{ backgroundColor: e.color }} onClick={() => onOffFunc(i)}>{e.name}</button><br /></div>);

    const onOffFunc = (i) => {
        if (productList[i].color === RED) {
            const productLight = [...productList];
            productLight[i].color = GREEN;
            setProductList(productLight);
        } else {
            const productLight = [...productList];
            productLight[i].color = RED;
            setProductList(productLight);
        }

    }
    
  const chooseProduct = () =>{
    addProduct(product, RED)
    setFlag(false)
  }


    const show = () => {
        if (flag === true) {
            return <div>
                <div>Room name: {roomName}</div>
                <br />
                <div>Room type: {roomType} </div>
                <br />
                <select value={product} onChange={(e) => setProduct(e.target.value)} className='addroombutton'>
                    <option value={AC}>{AC}</option>
                    <option value={LIGHT}>{LIGHT}</option>
                    <option value={STERO_SYSTEM}>{STERO_SYSTEM}</option>
                    <option value={BOILER}>{BOILER}</option>
                </select>
                <br />
                <button onClick={() => chooseProduct()}>Select Product</button>
            </div>
        } else {
            return <div>
                <div>Room name: {roomName}</div>
                <br />
                <div>Room type: {roomType} </div>
                <br />
                {showProducts}
                <button onClick={() => setFlag(true)}>Add New Product</button>
            </div>

        }
    }


    return (
        <div>
            {show()}
            <br />

        </div>
    )
}
